body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.disk {
  border-radius: 0.5rem;
  margin: 0.1rem;
  background-color: red;
  max-height: 40px;
}

.tower-background.flex-container {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.tower-background {
  width: 33%;
  height: 100%;
  background: linear-gradient(90deg, transparent 48%, #4a4a4a 48% 52%, transparent 52%);
  margin: 0 20px;
}

.tower {
  width: 100%;
}

.tower-disks {
  width: 85%;
}

.tower-base {
  height: 20px;
  width: 100%;
  background-color: #4a4a4a;
  border-radius: 0.5rem;
  padding: 5px;
}

.end-screen {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  scroll: hidden;
  background: rgba(0, 0, 0, 0.7);
}

.end-screen-content {
  background: white;
  position: relative;
  margin: 50px auto;
  height: 30%;
  width: 50%;
  border-radius: 0.5rem;
  box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.1);
}

.close-modal {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.close-modal:hover {
  color: rgba(0, 0, 0, 0.6);
}

.button {
  font-size: 1rem;
	padding: 5px;
  border-radius: 3px;
  cursor: pointer;
}

.button:hover {
  background-color: #eee;
}

.game.flex-container {
  -webkit-align-items: flex-end;
          align-items: flex-end;
  width: 80%;
  height: 300px;
  margin: 30px;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 5px 5px 20px 0px #ccc;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.vertical {
  -webkit-flex-direction: column;
          flex-direction: column;
}

